/* usage : 

    @media VARIABLE_NAME {
        // style in here 
    }
*/
html {
  box-sizing: border-box;
  font-size: 100%;
  font-family: "Rubik", sans-serif;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  padding: 4rem 1rem;
  background-color: #f5f6fa;
  color: #000000;
  font-family: "Rubik", sans-serif;
  line-height: 1.3;
}

main {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  padding: 0rem;
  max-width: 730px;
  width: 100%;
  margin: 0 auto;
}
main .comments {
  width: 100%;
}
main .comments .thread {
  display: flex;
  flex-flow: column nowrap;
  width: 100%;
}
main .comments .thread .comment {
  width: 100%;
  min-height: 144px;
  background-color: #ffffff;
  padding: 1.5rem 1.5rem;
  border-radius: 0.8rem;
  display: grid;
  column-gap: 1.5rem;
  margin-bottom: 1rem;
}
@media only screen and (max-width: 620px) {
  main .comments .thread .comment {
    padding: 1rem 1rem;
    column-gap: 0;
    row-gap: 1rem;
  }
}
main .comments .thread .comment .votes {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-around;
  grid-row-start: 1;
  grid-row-end: 3;
  width: 40px;
  height: 100px;
  border-radius: 0.5rem;
  background-color: #f5f6fa;
  text-align: center;
  padding: 0.2rem;
  transition-duration: 100ms;
  margin-top: 0.6rem;
}
@media only screen and (max-width: 620px) {
  main .comments .thread .comment .votes {
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 4;
    grid-row-end: 5;
    width: 100px;
    height: 40px;
    flex-flow: row nowrap;
    margin-top: 0;
    align-items: center;
  }
}
main .comments .thread .comment .votes .upvote {
  opacity: 0.6;
  transition-duration: 100ms;
}
main .comments .thread .comment .votes .upvote:hover {
  cursor: pointer;
  opacity: 1;
}
main .comments .thread .comment .votes .voteNumber {
  color: #5457b6;
  font-weight: 500;
}
main .comments .thread .comment .votes .downvote {
  margin-top: -7px;
  margin-bottom: 4px;
  opacity: 0.6;
  transition-duration: 100ms;
}
main .comments .thread .comment .votes .downvote:hover {
  cursor: pointer;
  opacity: 1;
}
main .comments .thread .comment .commentInfo {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  grid-column-start: 2;
  grid-column-end: 3;
}
@media only screen and (max-width: 620px) {
  main .comments .thread .comment .commentInfo {
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 2;
  }
}
main .comments .thread .comment .commentInfo .icon {
  height: 2rem;
  width: 2rem;
}
main .comments .thread .comment .commentInfo .username {
  margin-left: 1rem;
  font-weight: 500;
  color: #324152;
}
main .comments .thread .comment .commentInfo .you {
  margin-left: 0.5rem;
  height: 19px;
  width: 39px;
  background-color: #5457b6;
  color: #ffffff;
  font-weight: 500;
  text-align: center;
  font-size: 0.8rem;
  border-radius: 0.2rem;
}
main .comments .thread .comment .commentInfo .date {
  margin-left: 1rem;
  color: #67727e;
}
@media only screen and (max-width: 620px) {
  main .comments .thread .comment .commentInfo .date {
    margin: 0;
    margin-left: 1rem;
  }
}
main .comments .thread .comment .action {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  transition-duration: 100ms;
  grid-column-start: 3;
  grid-column-end: 4;
  justify-self: end;
}
@media only screen and (max-width: 620px) {
  main .comments .thread .comment .action {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 4;
    grid-row-end: 5;
  }
}
main .comments .thread .comment .action > .reply {
  transition-duration: 200ms;
}
main .comments .thread .comment .action > .reply:hover {
  cursor: pointer;
  opacity: 0.6;
}
main .comments .thread .comment .action > .reply > span {
  color: #5457b6;
  font-weight: 500;
  margin-left: 0.3rem;
}
main .comments .thread .comment .action > .delete {
  margin-left: 1rem;
  transition-duration: 200ms;
}
main .comments .thread .comment .action > .delete:hover {
  cursor: pointer;
  opacity: 0.6;
}
main .comments .thread .comment .action > .delete > span {
  color: #ed6468;
  font-weight: 500;
  margin-left: 0.3rem;
}
main .comments .thread .comment .action > .edit {
  margin-left: 1rem;
  transition-duration: 200ms;
}
main .comments .thread .comment .action > .edit:hover {
  cursor: pointer;
  opacity: 0.6;
}
main .comments .thread .comment .action > .edit > span {
  color: #5457b6;
  font-weight: 500;
  margin-left: 0.3rem;
}
main .comments .thread .comment .content {
  color: #67727e;
  grid-row-start: 2;
  grid-row-end: 3;
  grid-column-start: 2;
  grid-column-end: 4;
}
@media only screen and (max-width: 620px) {
  main .comments .thread .comment .content {
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 2;
    grid-row-end: 3;
  }
}
main .comments .thread .comment .content > p {
  margin: 0;
  margin-top: 0.2rem;
}
@media only screen and (max-width: 620px) {
  main .comments .thread .comment .content > p {
    margin: 0;
  }
}
main .comments .thread .comment > button {
  grid-row-start: 4;
  grid-row-end: 5;
  grid-column-start: 3;
  grid-column-end: 4;
  justify-self: end;
  margin-top: 1rem;
}
@media only screen and (max-width: 620px) {
  main .comments .thread .comment > button {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 5;
    grid-row-end: 6;
    margin-top: 0;
  }
}
main .comments .thread .comment > textarea {
  grid-row-start: 3;
  grid-row-end: 4;
  grid-column-start: 2;
  grid-column-end: 4;
  margin: 0;
}
@media only screen and (max-width: 620px) {
  main .comments .thread .comment > textarea {
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 3;
    grid-row-end: 4;
  }
}
main .comments .thread .replys {
  border-left: 2px solid #eaecf1;
  padding-left: 2rem;
  margin-left: 2rem;
  margin-bottom: 1rem;
}
@media only screen and (max-width: 620px) {
  main .comments .thread .replys {
    margin-left: 1rem;
    padding-left: 1rem;
  }
}
main .comments .thread .replys > .comment:last-child {
  margin-bottom: 0;
}
main .comments .thread .replys > .comment > textarea {
  grid-row-start: 3;
  grid-row-end: 4;
  grid-column-start: 2;
  grid-column-end: 4;
  margin: 0;
}
@media only screen and (max-width: 620px) {
  main .comments .thread .replys > .comment > textarea {
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 3;
    grid-row-end: 4;
  }
}
main .comments .thread .replys > .commentor {
  margin-top: -0.5rem;
  margin-bottom: 0.5rem;
}
main .comments .thread .replys > .replys {
  border-left: none;
  padding-left: none;
  margin-left: none;
}
main .comments .thread > .commentor {
  margin-top: -0.5rem;
  margin-bottom: 0.5rem;
}
main .commentor {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  width: 100%;
  min-height: 144px;
  background-color: #ffffff;
  padding: 1.5rem;
  border-radius: 0.7rem;
  position: sticky;
  bottom: 1.5rem;
  transition-duration: 100ms;
  box-shadow: 2px 0px 5px 2px #5457b62e;
}
@media only screen and (max-width: 620px) {
  main .commentor {
    flex-flow: row wrap;
    padding: 1rem;
  }
}
main .commentor img {
  width: 40px;
  height: 40px;
  margin-top: 4px;
}
@media only screen and (max-width: 620px) {
  main .commentor img {
    order: 2;
  }
}
main .commentor.stuck {
  border: 1px solid lightgray;
  box-shadow: 1px 1px 1px 1px gray;
}
main .alertBlocker {
  position: fixed;
  height: 110vh;
  position: fixed;
  width: 100vw;
  transform: translateY(-5rem);
  background-color: #000000a3;
}
main .alertBlocker .deleteAlert {
  max-width: 400px;
  height: auto;
  background-color: #ffffff;
  border-radius: 0.5rem;
  display: flex;
  flex-flow: row wrap;
  padding: 2rem;
  position: relative;
  margin: 0 auto;
  transform: translateY(-50%);
  top: 50%;
}
main .alertBlocker .deleteAlert > h1 {
  font-size: 1.7rem;
  font-weight: 700;
  color: #324152;
  justify-self: start;
  text-transform: none;
}
main .alertBlocker .deleteAlert > p {
  color: #67727e;
}
main .alertBlocker .deleteAlert > button {
  color: #ffffff;
  border: none;
  border-radius: 0.5rem;
  padding: 0.5rem 1rem;
  width: 9.5rem;
  font-size: 1rem;
}
main .alertBlocker .deleteAlert > button:first-child {
  background-color: #67727e;
}
main .alertBlocker .deleteAlert > button:last-child {
  background-color: #ed6468;
  margin-left: auto;
}
main textarea {
  flex: 1;
  margin: 0 1rem;
  border: 1px solid #eaecf1;
  border-radius: 0.5rem;
  padding: 1rem 1.5rem;
  width: 100%;
  height: 100px;
  resize: none;
  color: #324152;
  font-size: 1rem;
  font-weight: 400;
  font-family: inherit;
  transition-duration: 100ms;
  caret-color: #324152;
  /* scrollbar styling only for chrome! */
}
@media only screen and (max-width: 620px) {
  main textarea {
    margin: 0 0 1rem 0rem;
    order: 1;
    flex-basis: 100%;
  }
}
main textarea::-webkit-scrollbar-track {
  background-color: #eaecf1;
  border-radius: 10px;
  margin: 10px;
}
main textarea::-webkit-scrollbar {
  width: 10px;
  margin: 2px;
}
main textarea::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-image: -webkit-gradient(linear, left bottom, left top, color-stop(0.44, #7a99d9), color-stop(0.72, #497dbd), color-stop(0.86, #1c3a94));
  cursor: pointer;
}
main textarea.rep {
  background-image: url(/images/icon-reply.svg);
  background-repeat: no-repeat;
  background-position: 6px 80px;
}
main textarea:focus {
  outline: none;
  border-color: #5457b6;
}
main textarea::placeholder {
  font-weight: 500;
}
main button {
  width: 104px;
  height: 48px;
  background-color: #5457b6;
  color: #ffffff;
  text-transform: uppercase;
  border: none;
  border-radius: 0.5rem;
  font-weight: 700;
  letter-spacing: 1px;
  font-size: 1.1rem;
  transition-duration: 100ms;
}
@media only screen and (max-width: 620px) {
  main button {
    order: 3;
  }
}
main button:hover {
  cursor: pointer;
  opacity: 0.7;
}

h1, h2, h3 {
  margin-top: 0px;
}

h1 {
  text-transform: uppercase;
  color: #000000;
}

p {
  font-size: 1rem;
}